.sidebar {
  background: #1d2531;
  font-weight: 600;
}
.sidebar h3 {
  color: #5b6779;
  text-transform: uppercase;
  font-size: 12px;
  margin: 30px 0 15px 30px;
}
.sidebar li {
  padding-left: 20px;
  transition: background-color 0.1s ease-in-out 0s;
  border-left: 4px solid transparent;
}
.sidebar li:hover,
.sidebar li.active {
  background: #313d4f;
  border-left: 4px solid #279bf3;
}
.sidebar li.active a {
  color: #fff;
}
.sidebar li.active a i {
  color: #279bf3;
}
.sidebar a {
  color: #b7c0cd;
  display: block;
  padding: 10px 0;
  transition: color 0.1s ease-in-out 0s;
  position: relative;
  margin-bottom: 5px;
}
.sidebar a i {
  position: relative;
  float: left;
  width: 40px;
  margin: -10px -10px;
  margin-right: 5px;
  overflow: hidden;
  line-height: 40px;
  text-align: center;
}
.sidebar a:hover {
  color: #fff;
}
.sidebar a:hover i {
  color: #279bf3;
}
.dashhead {
  padding: 30px 30px 0 30px;
}
.dashhead h1 {
  margin-bottom: 0;
  padding-bottom: 0;
}
.dashhead nav {
  clear: both;
  overflow: hidden;
}
.dashhead nav li {
  float: left;
  margin-right: 30px;
}
.dashhead nav li a {
  color: #7f8fa4;
  font-weight: 600;
  font-size: 14px;
  padding-bottom: 23px;
  display: block;
  transition: color 0.1s ease-in-out 0s;
  border-bottom: solid 3px transparent;
}
.dashhead nav li a. {
  color: #2b333e;
  border-bottom: solid 3px #2b333e;
  transition: border-color 0.1s ease-in-out 0s;
}
.dashhead nav li.active a,
.dashhead nav li:hover a {
  color: #2b333e;
  border-bottom: solid 3px #2b333e;
  transition: border-color 0.1s ease-in-out 0s;
}
.dashhead nav li:last-child a {
  padding-right: 0;
}
.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 0;
  text-align: center;
  z-index: 1000;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}
.loader.disabled {
  display: none;
}
.dimmer {
  display: none;
  position: absolute;
  top: 0 !important;
  left: 0 !important;
  width: 100%;
  height: 100%;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(255,255,255,0.7);
  border-radius: 4px;
  opacity: 0;
  line-height: 1;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-transition: background-color 0.5s linear;
  transition: background-color 0.5s linear;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  will-change: opacity;
  z-index: 1000;
}
.dimmer .loading {
  display: block;
  margin: 25% auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(53,53,53,0.2);
  border-right: 1.1em solid rgba(53,53,53,0.2);
  border-bottom: 1.1em solid rgba(53,53,53,0.2);
  border-left: 1.1em solid #353535;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
  z-index: 2000;
}
.dimmer .loading,
.dimmer .loading:after {
  border-radius: 50%;
  width: 5em;
  height: 5em;
}
.active.dimmer {
  display: block;
  opacity: 1;
}
.box {
  background: #fff;
  border: solid 1px #e6eaee;
  border-radius: 3px;
  padding: 30px 25px;
}
.box-head {
  padding: 20px 0 0 30px;
}
.box-content {
  padding: 30px;
}
.btn {
  display: inline-block;
  padding: 0.375rem 1rem;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.5;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 1px solid #ced0da;
  border-radius: 0.25rem;
  background: linear-gradient(to bottom, #fff 0%, #f2f4f7 100%);
  color: #7383a1;
}
.btn:hover {
  background: linear-gradient(to bottom, #f2f4f7 0%, #fff 100%);
}
.btn.green {
  background: #39b54a;
  background: linear-gradient(to bottom, #39b54a 0%, #34aa44 100%);
  color: #fff;
}
.btn.green:hover {
  background: #34aa44;
  background: linear-gradient(to bottom, #34aa44 0%, #39b54a 100%);
}
.btn.green.btn-border {
  border-color: #249533;
}
.btn.disabled,
.btn:disabled {
  cursor: not-allowed;
  opacity: 0.65;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
html,
body {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font: 14px 'Open Sans', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
  background: #eff3f6;
}
.bg-light {
  color: #354052;
  background: #fff;
}
a {
  text-decoration: none;
}
.container {
  height: 100%;
}
.row {
  display: flex;
  flex-flow: row wrap;
}
#app.row {
  display: flex;
  flex-flow: row wrap;
  height: 100%;
}
#app.row > div {
  height: 100%;
}
#app.row > div:first-child {
  width: 200px;
}
#app.row > div:last-child {
  width: calc(100% - 200px);
}
.half-column {
  width: calc(99.99% * 1/2 - (30px - 30px * 1/2));
}
.half-column:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}
.half-column:last-child {
  margin-right: 0;
}
.half-column:nth-child(2n) {
  margin-right: 0;
  float: right;
}
.half-column:nth-child(2n + 1) {
  clear: left;
}
.main-content {
  padding: 30px;
}
